import { Component, Injector, OnInit } from '@angular/core';
import { BaseListClass } from 'src/app/class/base-list-class';
import { URLz } from 'src/app/enums/url.enum';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './qr-receipt.component.html',
  styleUrls: ['./qr-receipt.component.css']
})
export class QrReceiptComponent extends BaseListClass implements OnInit {
  constructor(public injector: Injector) {
    super(injector);
  }
  ngOnInit() {
    this.initTables();
  }
  initTables() {
    this._reset(URLz.$)
    this._tbls[URLz.$].columns = ['name', 'date' ,'amount'];
    this._tbls[URLz.$].endpoint = URLz.QR_RECEIPT
    this.getData();
  }
  getData(){
    this._activeRoute.params.subscribe(params => {
      const id = params['id']
      this._http.get({
        endpoint: URLz.QR_RECEIPT,
        query: {
          id
        }
      }).subscribe(res => {
        const records = [res.data.row]
        this._tbls[URLz.$].dataSource.data = records
      })
    })

  }
}
