import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { PrintBlackComponent } from './print-black/print-black.component';
import { PrintColorComponent } from './print-color/print-color.component';
import { MaterialColorComponent } from './material-color/material-color.component';
import { MaterialBlackComponent } from './material-black/material-black.component';
import { AcknowledgementColorComponent } from './acknowledgement-color/acknowledgement-color.component';
import { AckMaterialColorComponent } from './ack-material-color/ack-material-color.component';
import { FaqComponent } from './faq/faq.component';
import { QrReceiptComponent } from './qr-receipt/qr-receipt.component';

const routes: Routes = [
  { path: 'cash_cheque_color', component: PrintColorComponent },
  { path: 'cash_cheque_black', component: PrintBlackComponent },
  { path: 'material_color', component: MaterialColorComponent },
  { path: 'material_black', component: MaterialBlackComponent },
  { path: 'acknowledgement_color', component: AcknowledgementColorComponent },
  { path: 'ack_material_color', component: AckMaterialColorComponent },
  { path: 'faq', component: FaqComponent },
  { path: 'qr_receipt/:id', component: QrReceiptComponent }

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BlankRoutingModule { }
