import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { URLz } from 'src/app/enums/url.enum';
import { environment } from 'src/environments/environment';
import {HTTPService} from '../../service/http.service';
import {FormHelperService} from '../../service/form-helper.service';

@Component({
  selector: 'di-component-name-here',
  templateUrl: './ack-material-color.component.html',
  styleUrls: ['../../styles/print-color.css']
})
export class AckMaterialColorComponent implements OnInit {

  public slipId;
  public currDateandTime;
  public printData : any = {};
  public batchName : any = '';
  public printBy : any = '';
  slip_type: string;

  constructor(
    private http : HTTPService,
    private helper : FormHelperService,
    public activeRoute: ActivatedRoute,
  ) { }

  ngOnInit(): void {

    this.currDateandTime = this.helper._getCurrentdatenadTime();
    this.batchName = this.printBy = this.helper._getuserPermission()?.profile?.name;
    this.slipId = this.activeRoute.snapshot.paramMap.get('slipId');
    this.slip_type = this.activeRoute.snapshot.paramMap.get('rec_t');
    this.getSystems(this.slipId ,this.slip_type);
  }

  getSystems(slipId , type){
    this.http.get({
      url: environment.TRANSACTION,
      endpoint: URLz.PRINT_ACK,
      query: {
        receipt_type :type,
        ack_no : slipId,
      }
    })
    .subscribe((res : any)=>{
      this.printData = res.data.records;
    })
  }
}
